/**
 *       .
 * @param {number} count -  .
 * @returns {string}    .
 */
export function countSelections(count: number = 0): string {
  const suffixes = new Map([
    ['one', ' '],
    ['two', ' '],
    ['few', ' '],
    ['many', ' '],
    ['other', ' '],
  ]);

  const pluralRules = new Intl.PluralRules('ru').select(count);
  const rule = suffixes.get(pluralRules) as string;

  return rule;
}

/**
 *         .
 * @param {Date} dateString - ,       ISO 8601 (YYYY-MM-DDTHH:mm:ss.sssZ).
 * @returns {string}     ,     .
 */
export function formatDate(dateString: Date): string {
  let timeAgo = '';
  const date = new Date(dateString).getTime();
  const currentDate = new Date().getTime();

  const timeDiff = Math.abs(currentDate - date);
  const days = Math.floor(timeDiff / (1000 * 60 * 60 * 24));

  switch (days) {
    case 0:
      timeAgo = ``;
      break;
    case 1:
      timeAgo = ``;
      break;
    default:
      timeAgo = `${days} ${countSelections(days)}`;
      break;
  }

  const options: Intl.DateTimeFormatOptions = {
    hour: 'numeric',
    minute: 'numeric',
    timeZoneName: 'short',
    timeZone: 'Europe/Moscow',
  };

  const formattedDate = new Intl.DateTimeFormat('ru-RU', options).format(date);

  return `${timeAgo}, ${formattedDate}`;
}
